<template>
  <div class="song-list">
    <ul v-if="songlist.length > 0">
      <li
        v-for="list in songlist"
        :key="list.id"
        :currentmusic="currentmusic"
        :currentstate="currentstate"
        @click="$router.push({path:'/songlist',query:{id:list.id}})"
      >
        <img :src="list.coverImgUrl" alt="" />
      </li>
    </ul>
    <load-animation v-else></load-animation>
  </div>
</template>

<script>
import LoadAnimation from "../components/LoadAnimation.vue";
export default {
  components: { LoadAnimation },
  props:{
    currentmusic:{
      type:Object,
      default:()=>{return null}
    },
    currentstate:{
      type:Boolean,
      default:true
    }
  },
  data() {
    return {
      songlist: [], //存储所有榜单
    };
  },
  created() {
    this.getSongList();
  },
  methods: {
    //同步获取所有榜单数据
    async getSongList() {
      const res = await this.axios.get("/toplist");
      console.log(res);
      this.songlist =res.list;
      // .then((res) => {
      //   // console.log(res.list);
      //   this.songlist = res.list;
      // });
    },
  },
};
</script>

<style lang="scss" scoped>
.song-list {
  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    li {
      width: 30%;
      margin: 10px 0px;
      img {
        width: 100%;
        display: block;
        border-radius: 22px;
      }
    }
  }
}
</style>